
const IS_BROWSER = typeof window !== 'undefined' && window.name !== 'nodejs';
const IS_REACT18 = process.env.IS_REACT18 === 'true';
const MOUNT_ID = 'root';

import React from 'react';

import ReactDOM from 'react-dom/client';

import { createApp, bootstrap } from '@modern-js/runtime';

import { routes } from '@_modern_js_internal/main/routes';
const App = false;
import { router } from '@modern-js/runtime/plugins';


let AppWrapper = null;

let root = null;

function render() {
  
  const finalAppConfig = {
    ...App.config,
    ...typeof config === 'function' ? config() : {},
  }

  AppWrapper = createApp({
    plugins: [
     router({...{"serverBase":["/"],"routesConfig": { routes, globalApp: App },}, ...finalAppConfig?.router}),
    ]
  })()


  if(!AppWrapper.init && typeof appInit !== 'undefined') {
    AppWrapper.init = appInit;
  }


  if (IS_BROWSER) {
    bootstrap(AppWrapper, MOUNT_ID, root, ReactDOM);
  }

  return AppWrapper

}

AppWrapper = render();

export default AppWrapper;;
